import styled from 'styled-components'

import { Colors } from '../styling'

interface IHeadlineProps {
  margin?: string
  size?: 'smaller'
  light?: boolean
}

const getMargin = ({ margin = '0 0.5rem 0 0.5rem' }: IHeadlineProps) => margin

const getFontSize = ({ size }: IHeadlineProps) => size === 'smaller' && '1rem'

const getColor = ({ light = false }: IHeadlineProps) =>
  light ? Colors.greyLight : Colors.greyDarkest

export const Headline = styled.h2`
  color: ${getColor};
  font-weight: lighter;
  margin: ${getMargin};
  font-size: ${getFontSize};
`
